* {
  font-family: "Roboto", sans-serif; }

header {
  position: relative; }

nav {
  height: 50px;
  background-color: #212121; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none;

  .menu_link {
    font-size: 15px;
    font-weight: 300;
    color: #ffffff;
    text-decoration: none; }

  .menu_item {
    border-right: 2px dotted black;
    padding-right: 44px; } }

.subheader {
  position: absolute;
  width: 100%;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);

  .subheader_logo {
    display: block;
    margin-top: 16px;
    max-width: 100%; }

  .subheader_official {
    margin-top: 6px;
    opacity: 0.5;
    font-size: 13px;
    font-weight: 100;
    color: #ffffff; }

  .subheader_call {
    margin-top: 20px;
    font-size: 12px;
    font-weight: 300;
    color: #ffffff;
    text-align: right; }

  .subheader_phone {
    display: block;
    margin-top: 6px;
    font-size: 22px;
    text-decoration: none;
    font-weight: 400;
    text-transform: uppercase;
    color: #ffffff;
    line-height: 12px;
    text-align: right; }

  .subheader_btn {
    margin-top: 20px;
    width: 178px;
    height: 52px;
    background-image: linear-gradient(0deg, #1eacc7 0%, #4ce2ff 100%);
    border: none;
    border-radius: 4px;
    box-shadow: inset 0px -3px 0px 0px rgba(0, 0, 0, 0.11);
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.2);
    color: #ffffff; } }

.promo {
  min-height: 800px;
  background: url("../img/main_bg.jpg") center (center / cover) no-repeat;
  padding: 150px 0 135px 0;

  .promo_header {
    margin: 0;
    margin-top: 67px;
    font-size: 60px;
    line-height: 60px;
    text-align: center;
    font-weight: 700;
    text-transform: uppercase;
    color: #ffffff; }

  .promo_subheader {
    margin: 0;
    margin-top: 22px;
    font-size: 36px;
    line-height: 36px;
    text-align: center;
    font-weight: 700;
    color: #ffffff; }

  .promo_discr {
    margin-top: 23px;
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    color: #ffffff;
    text-align: center; }

  .promo_btn {
    display: block;
    margin: 118px auto;
    width: 265px;
    height: 69px;
    background-image: linear-gradient(0deg, #1eacc7 0%, #4ce2ff 100%);
    border: none;
    border-radius: 4px;
    box-shadow: inset 0px -4px 0px 0px rgba(0, 0, 0, 0.11);
    font-size: 18px;
    font-weight: 400;
    text-transform: uppercase;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.2);
    color: #ffffff; } }

.work {
  min-height: 900px; }

.label {
  margin: 71px auto 0 auto;
  background-color: #000000;
  width: 112px;
  height: 24px;
  padding: 5px;
  font-size: 11px;
  text-align: center;
  font-weight: 100;
  text-transform: uppercase;
  color: #ffffff; }

.label_two {
  margin-top: 38px;
  text-align: center;
  font-size: 38px;
  line-height: 28px;
  font-weight: 700;
  text-transform: uppercase;
  color: #222222; }

.subtitle {
  margin-top: 19px;
  font-size: 17px;
  line-height: 21px;
  font-weight: 400;
  color: #222222; }

.work {
  .container {
    padding: 0 40px 0 40px; }

  .row {
    margin: 0; }

  .cause_block {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 35px; } }

.cause_block img {
  width: 113px;
  height: 113px;
  margin-right: 26px; }

.cause_descr {
  width: 343px;

  .title {
    font-size: 17px;
    line-height: 21px;
    font-weight: 400;
    color: #1eacc7; }

  .text {
    font-size: 15px;
    font-weight: 300;
    color: #202020; } }
